<template>
  <div>
    <el-breadcrumb separator-icon="ArrowRight">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-divider />
    <el-table :data="tableData">
      <el-table-column fixed prop="id" label="" width="100"/>
      <el-table-column prop="l" label="" width="150"/>
      <el-table-column prop="cs_mm" label="" width="150"/>
      <el-table-column prop="fs_mm" label="" width="150"/>
      <el-table-column prop="mn_mm" label="" width="150"/>
      <el-table-column prop="cl_sm" label="" width="150"/>
      <el-table-column prop="out_te" label="" width="150"/>
      <el-table-column prop="s_el" label="" width="150"/>
      <el-table-column prop="sn_qm" label="" width="150"/>
      <el-table-column prop="ud_qm" label="Mo" width="150"/>
      <el-table-column prop="ni_qm" label="Ni" width="150"/>
      <el-table-column prop="oe_qm" label="Mn" width="150"/>
      <el-table-column prop="po_qm" label="Cr" width="150"/>
      <el-table-column prop="c_qm" label="C" width="150"/>
      <el-table-column prop="si_qm" label="Si" width="150"/>
      <el-table-column prop="ws_mm" label="" width="150"/>
      <el-table-column fixed="right" label="" width="120">
        <template #default="scope">
          <el-button
              type="text"
              size="small"
              @click.prevent="deleteRow(scope.row.id,scope.$index)">
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        @current-change="handleCurrentChange"
        :page-size="pageSize"
        :pager-count="7"
        layout="prev, pager, next"
        :total="total"
        style="float: right;"
    />
  </div>
</template>

<script lang="js">

import axios from "axios";
import {ElMessage} from "element-plus";

export default {
  data() {
    return {
      tableData: [],
      total: 0,
      pageSize: 20,
      currentPage: 1
    };
  },
  methods: {
    async getTotal(){
      let total;
      let url = 'http://' + '81.70.243.180' + ':8081/get_slab_steel_train/getPage';
      await axios.post(url)
          .then(function (value) {
            total=value;
          })
          .catch(function (reason) {
            ElMessage({
              message: reason,
              center: true,
              type: 'error',
            });
          });
      this.total = total.data;
    },

    async getData() {

      let url = 'http://' + '81.70.243.180' + ':8081/get_slab_steel_train/findByPage?';
      url += 'currentPage=' + this.currentPage;
      url += '&pageSize=' + this.pageSize;
      let res;
      await axios.post(url)
          .then(function (value) {
            res=value;
            console.log(typeof res.data);
          })
          .catch(function (reason) {
            ElMessage({
              message: reason,
              center: true,
              type: 'error',
            });
          });
      this.tableData = res.data;
    },

    async deleteData(id){
      let url = 'http://' + '81.70.243.180' + ':8081/get_slab_steel_train/deleteById?id=' + id;
      await axios.post(url).then(function (){
        ElMessage({
          message: '',
          center: true,
          type: 'success',
        });

      })
          .catch(function (reason) {
            console.log(reason);
            ElMessage({
              message: reason,
              center: true,
              type: 'error',
            });
          });
      this.getData();
      this.getTotal();
    },

    init() {
      this.getData();
      this.getTotal();
    },

    deleteRow(id,index) {
      this.tableData.splice(index, 1);
      this.deleteData(id);
    },

    handleCurrentChange(val) {
      this.currentPage = val;
      this.getData();
    },
  },
  mounted() {
    this.init();
  }
}


</script>
